.home-sticky {
	min-height: 50px;
}

.h-32px {
	height: 32px !important;
}

@media (max-width: 768px) {
	.home-sticky-ad {
		padding: 10px 0;
	}
	.event-banner {
		padding: 10px 5px;
	}
	.heading-4-regular {
		font-size: 14px; /* Adjust font size for smaller devices */
		margin: 0;
	}
	.fw-500.fs-10px {
		font-size: 12px; /* Increase text size for smaller screens */
	}
	.col-auto {
		flex: 0 0 auto; /* Ensure columns adjust properly on smaller screens */
	}
	.fs-12px {
		font-size: 12px; /* Ensure button text size is legible */
	}
	.fs-14px {
		font-size: 14px; /* Default font size for larger screens */
	}
	.h-32px {
		height: 32px; /* Button height remains consistent */
	}
}

@media (max-width: 768px) {
	.d-md-none {
		display: block !important;
	}
	.d-none {
		display: none !important;
	}
}
